@import url('https://fonts.googleapis.com/css2?family=Anton&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap');

html, body {
    font-family: 'Anton', sans-serif;
    height: 100vh;
    width: 100vw;
    background-color: rgb(0, 0, 0);
}

.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    /* padding: 1.5%; */
    width: 100vw;
}

.section-1 {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    /* padding: 1%; */
    height: 100vh;
    width: 100%;
    /* background-color: #F37878; */
}

.section-1 .bg {
    position: absolute;
    width: 100vw;
    height: 100vh;
    filter: blur(5px);
}

.section-1-child {
    width: max-content;
    height: max-content;
    margin-bottom: 50px;
    z-index: 1;
}

.section-1 .head-con {
    height: max-content;
    width: max-content;
}

.section-1 .head-con .head {
    color: #FFF;
    font-size: 4rem;
    font-weight: 400;
    letter-spacing: 0.15rem;
}

.section-1 .searchbar-con {
    font-family: 'Quicksand', sans-serif;
    margin-top: 40px;
    /* text-align: center; */
    width: 100%;
}

.section-1 .searchbar-con input {
    font-weight: 500;
    font-size: 1.1rem;
    letter-spacing: 0.11rem;
    height: 80px;
    width: 60%;
    border-radius: 100px;
    margin-right: 20px;
    padding-left: 30px;
    border: 1px solid #000;
}

.section-1 .searchbar-con button {
    font-size: 1.4rem;
    font-weight: 700;
    height: 80px;
    width: 100px;
    border: none;
    border-radius: 100px;
}

.section-2 {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    width: 100%;
    padding: 0 4px;
    background-color: #FFF;
}

.search-result-child {
    margin-top: 70px;
    /* flex: 35%; */
    /* max-width: 35%; */
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}

.photos {
    margin: 8px;
    /* vertical-align: middle; */
    /* width: 100%; */
    /* grid-column: 2; */
}